@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Urbanist:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&family=Montserrat:wght@300&family=Playfair+Display&family=Scheherazade+New&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&family=Urbanist:wght@300&display=swap);
*{margin:0;padding:0}body{display:flex;align-items:center;justify-content:center;height:100vh;background-color:#121212}.watch-container{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;display:grid;grid-template-rows:repeat(11, 40px);grid-template-columns:repeat(11, 40px);color:gray}.item{text-align:center;font-size:30px}footer{font-family:'Indie Flower';width:calc(100% - 20px)}footer a{text-decoration:none}footer i{background-color:transparent;color:white}footer .madeby,footer .icons{font-size:30px;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px}footer i:hover{-webkit-transform:scale(0.8);transform:scale(0.8)}@media screen and (max-width: 600px){.watch-container{grid-template-columns:repeat(11, 20px);grid-template-rows:repeat(11, 20px)}.item{font-size:15px}footer .icons,footer .madeby{grid-column-gap:12px;-webkit-column-gap:12px;column-gap:12px;font-size:18px}}

